import React from 'react'

const Numbers = ({ persons, onclick }) => {
    return (
        <div>
            <h2>Numerot</h2>
            <ul>
                {persons.map(person =>
                    <p key={person.name}>{person.name} {person.number}
                        <button id={person.id} name={person.name} onClick={onclick}>poista</button>
                    </p>
                )
                }
            </ul>
        </div>
    )

}

export default Numbers