import React from 'react'

const Adding = ({addperson,newname,newnumber,changename,changenumber }) => {
    return(
        <div>
        
        <form onSubmit={addperson}>
            <div>
                nimi: <input value={newname} onChange={changename} />
            </div>
            <div>
                numero: <input value={newnumber} onChange={changenumber} />
            </div>
            <div>   
                <button type="submit">lisää</button>
            </div>
        </form>
        </div>
    )
}

export default Adding