import React from 'react';
import Numbers from './components/Numbers'
import Adding from './components/Add'
import personService from './services/persons'

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            persons: [],
            newName: '',
            newNumber: ''
        }
    }
    componentDidMount() {

        personService
            .getAll()
            .then(response => {
                this.setState({ persons: response })
            })
    }

    addPerson = (event) => {
        
        event.preventDefault()
        if (!this.state.persons.some(person => person.name === this.state.newName)) {
            const personObject = {
                name: this.state.newName,
                number: this.state.newNumber
            }

            personService
                .create(personObject)
                .then(response => {
                    this.setState({
                        persons: this.state.persons.concat(response),
                        newName: '',
                        newNumber: ''
                    })
                })

        } else {
            alert("Nimi jo luettelossa")
        }

    }
    removePerson = (event) => {
        event.preventDefault()
        const id = event.target.getAttribute("id");
        const name = event.target.getAttribute("name");
        if (window.confirm("Poistetaanko "+name+"?")) {
            personService
                .remove(id)
                .then(person => {
                    this.setState({
                        persons:
                            this.state.persons.filter(person => person.id.toString() !== id)
                    });

                })
                .catch(error => {
                    console.log("couldn't remove")
                    personService
                        .getAll()
                        .then(persons => this.setState({
                            persons: persons,
                            newName: '',
                            newNumber: ''
                        }));
                });
        };
    };

    handleNameChange = (event) => {
        this.setState({ newName: event.target.value })
    }
    handleNumChange = (event) => {
        this.setState({ newNumber: event.target.value })
    }

    render() {
        return (
            <div>
                <h2>Puhelinluettelo</h2>
                <Adding
                    addperson={this.addPerson}
                    newname={this.state.newName}
                    newnumber={this.state.newNumber}
                    changename={this.handleNameChange}
                    changenumber={this.handleNumChange}
                />
                <Numbers 
                    persons={this.state.persons} 
                    onclick={this.removePerson}
                />

            </div>
        )

    }
}
export default App